import { useEffect, useRef } from 'react'
import invariant from 'tiny-invariant'
import p5 from 'p5'

export function P5Container<T extends Record<string, any>>({
  parameters,
  setup,
  draw,
  attributes
}: {
  parameters: T
  setup: (p: p5, parameters: T) => void
  draw: (p: p5, parameters: T) => void
  attributes?: React.HTMLAttributes<HTMLDivElement>
}) {
  const renderer = useRef<p5.Renderer | null>(null)
  const paramsRef = useRef<T>(parameters)
  const parentRef = useRef<HTMLDivElement>(null)

  const sketch = (p: p5) => {
    p.setup = () => {
      renderer.current = p.createCanvas(window.innerWidth, window.innerHeight)
      renderer.current.addClass('h-full w-full')
      p.windowResized = () => {
        invariant(parentRef.current)
        p.resizeCanvas(
          parentRef.current.clientWidth,
          parentRef.current.clientHeight
        )
      }
      setup(p, paramsRef.current)
    }
    p.draw = () => draw(p, paramsRef.current)
  }

  useEffect(() => {
    invariant(parentRef.current)
    const p = new p5(sketch, parentRef.current)
    return () => p.remove()
  }, [])

  return <div ref={parentRef} {...attributes}></div>
}
