import { icons } from 'app/assets'
import { useState } from 'react'

export default function PDF({
  src,
  className,
  dark
}: {
  src: string
  className?: string
  dark?: boolean
}) {
  const [expanded, setExpanded] = useState(false)

  return (
    <div
      className={
        'w-full relative mx-auto my-2 h-[calc(100vh-200px)]' + (className || '')
      }>
      <button className='absolute top-0 right-0 border border-white rounded p-1 m-2 bg-black z-40'>
        <a href={src} target='_blank'>
          <img src={icons.fullScreen} height={16} width={16} />
        </a>
      </button>
      <iframe
        src={src + '#view=FitBH'}
        className='mx-auto rounded relative w-full h-full'
        style={{
          filter: dark
            ? 'invert(96%)'
            : 'invert(22%) sepia(5%) hue-rotate(160deg) brightness(80%) contrast(120%)'
        }}></iframe>
    </div>
  )
}
