import { Outlet, useLocation } from '@remix-run/react'
import { Role, Tag, projects } from '../projects/projects'
import { lazy, useMemo, useState } from 'react'
import _ from 'lodash'
import Background from './Background'
import Boxes from './Boxes'
import { ClientOnlyImport } from 'app/services/components'
import { useIsMobile } from '~/services/hooks'

const LazyBackground = lazy(() => import('./Background'))
export default function Index() {
  const location = useLocation()
  const search = new URLSearchParams(location.search)
  const role = search.get('role') as Role | undefined
  const tag = role
    ? (search.get('tag') as Tag<typeof role>) || undefined
    : undefined

  const isMobile = useIsMobile()

  return (
    <div className='fixed top-0 left-0 w-screen h-screen'>
      {!isMobile && (
        <div className='fixed top-0 left-0 h-screen w-screen -z-10'>
          <ClientOnlyImport importChildren={() => <LazyBackground />} />
        </div>
      )}
      <Boxes role={role} tag={tag} />
    </div>
  )
}
