import { useLocation } from '@remix-run/react'
import { Link, useParams } from 'react-router-dom'
import { ProjectId, projects } from '../../projects/projects'

export default function Project() {
  const { projectId } = useParams() as { projectId: ProjectId }
  const location = useLocation()
  const project = projects[projectId]

  return (
    <div className='fixed top-0 left-0 h-full w-full p-8 z-10 flex items-center justify-center'>
      <Link to={'/' + location.search}>
        <div className='h-full w-full absolute top-0 left-0 bg-black/50'></div>
      </Link>
      <div className='backdrop-blur bg-black/50 rounded-lg border border-accent-light max-w-4xl max-h-full overflow-y-auto relative'>
        <div className='backdrop-blur w-fit px-4 mx-auto rounded-lg sticky top-8 mb-4'>
          <h2 className='font-bold text-lg text-center '>{project.title}</h2>
          <h3 className='text-base text-center'>{project.subtitle}</h3>
        </div>

        <div className='p-8'>
          <div className='mt-4'>{project.content}</div>
          <div className='mt-4 font-serif'>{project.description}</div>
        </div>
      </div>
    </div>
  )
}
