import _ from 'lodash'
import { lazy, useRef } from 'react'
import { ClientOnly } from 'remix-utils'
import invariant from 'tiny-invariant'
import { P5Container } from '../services/clientComponents'

const text = [
  'i think',
  'i have',
  'an idea',
  'something',
  'to write',
  'a question',
  'a poem',
  'what'
]

const MAX_GAP = 300
const MIN_SIZE_VW = 5
const MAX_SIZE_VW = 80
const MIN_OPACITY = 0
const MAX_OPACITY = 1

export default function Background() {
  const VwToFontSize = (targetWidthVw: number, text: string) => {
    const targetWidthPixels = (targetWidthVw / 100) * window.innerWidth
    const individualCharPixels = targetWidthPixels / text.length
    const fontSize = individualCharPixels * 2
    return _.round(fontSize, 1)
  }

  console.log('imported, running stuff')
  return (
    <P5Container
      parameters={{
        fragments: [] as {
          text: string
          x: number
          y: number
          size: number
          color: number
        }[],
        nextTrigger: _.random(MAX_GAP)
      }}
      setup={(p, params) => {
        p.background('black')
        p.colorMode('hsl', 1)
        p.textFont('Cormorant Garamond')
        for (let i = 0; i < 7; i++) {
          const textSample = _.sample(text) as string
          params.fragments.push({
            text: textSample,
            x: _.random(p.width),
            y: _.random(p.height),
            size: VwToFontSize(
              _.random(MIN_SIZE_VW, MAX_SIZE_VW, true),
              textSample
            ),
            color: _.random(MIN_OPACITY, MAX_OPACITY, true)
          })
        }
      }}
      draw={(p, params) => {
        const makeNewTextFrame = () => {
          const fragment = _.sample(params.fragments)
          invariant(fragment)
          const textSample = _.sample(text) as string
          fragment.text = textSample
          fragment.x = _.random(p.width)
          fragment.y = _.random(p.height)
          fragment.size = VwToFontSize(
            _.random(MIN_SIZE_VW, MAX_SIZE_VW, true),
            textSample
          )
          fragment.color = _.random(MIN_OPACITY, MAX_OPACITY)
        }
        if (p.millis() > params.nextTrigger) {
          makeNewTextFrame()
          params.nextTrigger += _.random(MAX_GAP)
        }
        p.background('black')
        for (let fragment of params.fragments) {
          const color = p.color('white')
          color.setAlpha(fragment.color / 2)
          p.fill(color)
          p.textSize(fragment.size)
          p.text(fragment.text, fragment.x, fragment.y)
        }
        for (let i = 0; i < _.random(200); i++) {
          const color = p.color('white')
          color.setAlpha(_.random(0, 0.4))
          p.fill(color)
          const [x, y] = [_.random(p.width), _.random(p.height)]
          p.circle(x, y, 4)
        }
      }}
    />
  )
}
