import { cloneElement, useEffect, useMemo, useRef, useState } from 'react'
import { ProjectId, Role, Tag, allTags, projects } from '../projects/projects'
import _ from 'lodash'
import Matter from 'matter-js'
import invariant from 'tiny-invariant'
import { q } from 'app/services/clientFunctions'
import {
  getTagRole as getTagRole,
  isProjectId,
  isRole,
  isTagOfType
} from 'app/types/typeGuards'
// @ts-ignore
import MatterAttractors from 'matter-attractors'
import { Link, useLocation } from '@remix-run/react'
import { useLocales } from 'remix-utils'
import useStateRef from 'react-usestateref'
import { useIsMobile } from '~/services/hooks'

Matter.use(MatterAttractors)

export default function Boxes({
  role,
  tag
}: {
  role: Role | undefined
  tag: Tag<Role> | undefined
}) {
  const shownProjects = useMemo(
    () =>
      _.map(
        _.entries(projects).filter(
          ([_id, project]) =>
            (!role || project.role === role) &&
            (!tag || project.tags.includes(tag))
        ),
        0
      ) as ProjectId[],
    [role, tag]
  )
  const shownTags = useMemo(
    () => (tag ? [tag] : !role ? _.union(..._.values(allTags)) : allTags[role]),
    [role, tag]
  )
  const shownRoles = useMemo(() => (role ? [role] : _.keys(allTags)), [role])

  const engine = useRef<Matter.Engine>()
  const nextAnimationCall = useRef<number>()
  const frame = useRef<HTMLDivElement>(null)
  const bodies = useRef<ReturnType<typeof createBody>[]>([])
  const constraints = useRef<
    {
      ids: [string, string]
      constraint: ReturnType<typeof Matter.Constraint.create>
    }[]
  >([])
  const pins = useRef<
    { id: string; constraint: ReturnType<typeof Matter.Constraint.create> }[]
  >([])

  const MAX_DISTANCE = 300

  const autoCreateConstraints = (id: string) => {
    if (isRole(id)) {
      if (!pins.current.find(pin => isRole(pin.id))) {
        createPin(
          id,
          { x: window.innerWidth / 2, y: window.innerHeight / 3 },
          { length: 0, stiffness: 0.01 }
        )
      }
      createPin(
        id,
        { x: window.innerWidth / 2, y: window.innerHeight / 2 },
        { length: 50, stiffness: 0.3 }
      )
      for (let role of _.keys(allTags).filter(
        role => shownRoles.includes(role) && role !== id
      )) {
        createConstraint(id, role, {
          length: Math.min(window.innerHeight / 3, window.innerWidth / 3),
          stiffness: 1
        })
      }

      return
    }
    for (let key of _.keys(allTags) as Role[]) {
      if (isTagOfType(id, key)) {
        createConstraint(id, key)
        return
      }
    }
    if (isProjectId(id)) {
      const tags = projects[id].tags
      tags.forEach(tag =>
        createConstraint(id, tag, { length: 200, stiffness: 0.2 })
      )
    }
  }

  const createPin = (
    id: string,
    { x, y }: { x: number; y: number },
    options?: Parameters<typeof Matter.Constraint.create>[0]
  ) => {
    const bodyA = bodies.current.find(body => body.id === id)?.body
    const pin = {
      id,
      constraint: Matter.Constraint.create({
        length: 0,
        stiffness: 1,
        ...options,
        bodyA,
        pointB: { x, y }
      })
    }
    pins.current.push(pin)
    invariant(engine.current)
    Matter.Composite.add(engine.current.world, pin.constraint)
  }

  const createConstraint = (
    idA: string,
    idB: string,
    options?: Parameters<typeof Matter.Constraint.create>[0]
  ) => {
    if (
      constraints.current.find(
        constraint =>
          constraint.ids.includes(idA) && constraint.ids.includes(idB)
      )
    )
      return
    const bodyA = bodies.current.find(body => body.id === idA)?.body
    const bodyB = bodies.current.find(body => body.id === idB)?.body
    invariant(bodyA && bodyB)
    const constraint = {
      ids: [idA, idB] as [string, string],
      constraint: Matter.Constraint.create({
        length: 100,
        stiffness: 0.5,
        ...options,
        bodyA,
        bodyB
      })
    }
    constraints.current.push(constraint)
    invariant(engine.current)
    Matter.Composite.add(engine.current.world, constraint.constraint)
    return constraint
  }

  const isMobile = useIsMobile()

  const createBody = (
    id: string,
    xDecimal: number,
    yDecimal: number,
    options?: Parameters<typeof Matter.Bodies.rectangle>[4]
  ) => {
    invariant(frame.current && engine.current)
    const el = q(`[data-matter-id=${id}]`)
    const body = {
      body: Matter.Bodies.rectangle(
        xDecimal * frame.current.clientWidth,
        yDecimal * frame.current.clientHeight,
        el.clientWidth,
        el.clientHeight,
        {
          frictionAir: 0.2,
          collisionFilter: {
            group: -1,
            mask: 0,
            category: 2
          },
          label: id,
          ...options,
          plugin: {
            ...options?.plugin,
            attractors: isMobile
              ? undefined
              : [
                  function (bodyA: Matter.Body, bodyB: Matter.Body) {
                    if (isProjectId(bodyB.label) !== isProjectId(bodyA.label))
                      return
                    const xDiff = bodyA.position.x - bodyB.position.x
                    const yDiff = bodyA.position.y - bodyB.position.y
                    const distance = Math.max(
                      0,
                      MAX_DISTANCE -
                        Matter.Vector.magnitude({ x: xDiff, y: yDiff })
                    )
                    const direction = Matter.Vector.normalise({
                      x: xDiff,
                      y: yDiff
                    })
                    return {
                      x: direction.x * distance * -1 * 1e-3,
                      y: direction.y * distance * -1 * 1e-3
                    }
                  }
                ]
          }
        }
      ),
      id,
      el,
      render() {
        const { x, y } = this.body.position
        this.el.style.top = `${y - this.el.clientHeight / 2}px`
        this.el.style.left = `${x - this.el.clientWidth / 2}px`

        if (isMobile) return

        const diffX = this.body.position.x - window.innerWidth / 2
        const diffY = this.body.position.y - window.innerHeight / 2
        const distance = Math.max(
          0,
          400 - Matter.Vector.magnitude({ x: diffX, y: diffY })
        )
        Matter.Body.applyForce(this.body, this.body.position, {
          x: diffX * distance * 1e-6,
          y: diffY * distance * 1e-6
        })
      }
    }

    bodies.current.push(body)
    Matter.Composite.add(engine.current.world, body.body)
    return body
  }

  const createWorld = () => {
    console.log('RECREATING WORLD')
    console.trace()

    engine.current = Matter.Engine.create({
      gravity: { x: 0, y: 0 }
    })

    createBody('headline', 0.5, 0.1, {
      isStatic: true
    })

    updateBodyCount()

    const rerender = () => {
      invariant(engine.current)
      for (let body of bodies.current) body.render()

      Matter.Engine.update(engine.current)
      nextAnimationCall.current = requestAnimationFrame(rerender)
    }

    rerender()
  }

  const oldWidth = useRef(0)
  const oldHeight = useRef(0)

  const onResize = () => {
    const newWidth = window.innerWidth
    const newHeight = window.innerHeight
    const xSizeRatio = newWidth / oldWidth.current
    const ySizeRatio = newHeight / oldHeight.current
    for (let body of bodies.current) {
      Matter.Body.setPosition(body.body, {
        x: body.body.position.x * xSizeRatio,
        y: body.body.position.y * ySizeRatio
      })
    }
    for (let pin of pins.current) {
      pin.constraint.pointB = {
        x: pin.constraint.pointB.x * xSizeRatio,
        y: pin.constraint.pointB.y * ySizeRatio
      }
    }
    oldWidth.current = newWidth
    oldHeight.current = newHeight
  }

  useEffect(() => {
    createWorld()
    oldWidth.current = window.innerWidth
    oldHeight.current = window.innerHeight
    window.addEventListener('resize', onResize)
    return () => {
      nextAnimationCall.current &&
        cancelAnimationFrame(nextAnimationCall.current)
      engine.current && Matter.Engine.clear(engine.current)
      bodies.current = []
      constraints.current = []
      pins.current = []
      window.removeEventListener('resize', onResize)
    }
  }, [])

  const updateBodyCount = () => {
    const bodyIds = bodies.current.map(body => body.id)
    const shownIds = [...shownRoles, ...shownTags, ...shownProjects, 'headline']

    const newBodies = _.difference(shownIds, bodyIds)
    const deletedBodyIds = _.difference(bodyIds, shownIds)

    for (const newBodyId of newBodies) {
      createBody(newBodyId, Math.random(), Math.random())
    }
    for (const newBodyId of newBodies) {
      autoCreateConstraints(newBodyId)
    }

    const deletedBodies = _.filter(bodies.current, body =>
      deletedBodyIds.includes(body.id)
    )

    invariant(engine.current)

    for (const deletedBody of deletedBodies) {
      for (const constraint of constraints.current.filter(constraint =>
        constraint.ids.includes(deletedBody.id)
      )) {
        Matter.Composite.remove(engine.current.world, constraint.constraint)
        _.pull(constraints.current, constraint)
      }

      for (const pin of pins.current.filter(pin => pin.id === deletedBody.id)) {
        Matter.Composite.remove(engine.current.world, pin.constraint)
        _.pull(pins.current, pin)
      }

      Matter.Composite.remove(engine.current.world, deletedBody.body)
      _.pull(bodies.current, deletedBody)
    }
  }

  useEffect(() => {
    if (nextAnimationCall.current) updateBodyCount()
  }, [shownRoles, shownTags, shownProjects])

  const location = useLocation()
  const setParams = (keys: Partial<Record<'tag' | 'role', string | null>>) => {
    const params = new URLSearchParams(location.search)
    _.entries(keys).forEach(([key, value]) =>
      value ? params.set(key, value) : params.delete(key)
    )

    return `${location.pathname}?${params.toString()}`
  }

  const NAV_BUTTON =
    'bg-accent-light hover:bg-accent-dark mouse:hover:border-accent-light border border-transparent transition-[background,transform] mouse:hover:scale-125 duration-300 w-fit px-2 rounded-full flex z-10 drop-shadow-xl'

  return (
    <div
      className='child:absolute h-full w-full absolute top-0 left-0 child:whitespace-nowrap'
      ref={frame}>
      {shownProjects.map(projectId => {
        return (
          <div
            key={projectId}
            data-matter-id={projectId}
            className='rounded-full h-[33vh] w-[33vh] overflow-hidden relative opacity-50 hover:opacity-100 hover:scale-125 transition-[opacity,transform,top,left] duration-500 cursor-pointer hover:z-10'>
            <Link to={'/' + projectId + location.search}>
              {cloneElement(projects[projectId].background, {
                autoPlay: true,
                muted: true,
                loop: true,
                className: 'object-cover w-full h-full'
              })}
            </Link>
          </div>
        )
      })}
      <button data-matter-id='headline' className={`text-xl ${NAV_BUTTON}`}>
        <Link to={'/'}>Joshua Tazman Reinier</Link>
      </button>
      {shownRoles.map(role => (
        <button
          key={role}
          data-matter-id={role}
          className={`text-lg ${NAV_BUTTON}`}>
          <Link to={setParams({ role: role, tag: null })}>{role}</Link>
        </button>
      ))}
      {shownTags.map(tag => (
        <button key={tag} data-matter-id={tag} className={NAV_BUTTON}>
          <Link to={setParams({ tag: tag, role: getTagRole(tag) })}>{tag}</Link>
        </button>
      ))}
    </div>
  )
}
