import { ProjectId, Role, Tag, allTags, projects } from 'app/projects/projects'
import _ from 'lodash'

export const isTagOfType = <T extends Role>(
  tag: string,
  role?: T
): tag is Tag<T> => {
  return (
    role ? allTags[role] : (_.values(allTags).flat() as string[])
  ).includes(tag)
}

export const getTagRole = (tag: Tag<Role>) =>
  (_.keys(allTags) as Role[]).find(role =>
    (allTags[role] as string[]).includes(tag)
  )

export const isRole = (role: string): role is Role =>
  _.keys(allTags).includes(role)

export const isProjectId = (projectId: string): projectId is ProjectId =>
  _.keys(projects).includes(projectId)
