import _ from 'lodash'
import p5, { p5InstanceExtensions } from 'p5'
import { useEffect, useRef, useState } from 'react'
import invariant from 'tiny-invariant'
import { checkIsMobile } from './clientFunctions'

export const useWindow = (): _.Omit<Partial<Window>, 'document'> & {
  document: Partial<Document>
} => {
  const [windowObject, setWindowObject] = useState<Window | null>(null)
  useEffect(() => {
    setWindowObject(window)
  }, [])
  return windowObject ? windowObject : { document: {} }
}

export const useIsMobile = () => {
  const [isMobile, setIsMobile] = useState(false)
  useEffect(() => {
    setIsMobile(checkIsMobile())
  }, [])
  return isMobile
}
